<template>
    <div id="projectsPage">
        <div class="container-fluid">
            <div id="projectsContainer">
                <div class="row">
                    <div class="col-md-12">
                        <h2 id="projectsHeader" class="pt-4">Projects</h2>
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-12">
                        <div id="projectsItemList">
                            <div class="row d-flex align-items-stretch">
                                <div class="col-lg-4 col-md-6 d-flex" v-for="project in projects" v-bind:key="project.id">
                                    <div class="projectsItem w-100" @click="OpenProject(project.demoLink)">
                                        <div class="projectsItemImageDiv">
                                            <img :src="project.image" class="projectsItemImage" loading="lazy" :alt="project.title + ' Image'">
                                        </div>
                                        <div class="projectsItemContent">
                                            <p class="projectsItemTitle">{{ project.title }}</p>
                                            <p class="projectsItemLinks">
                                                <a v-for="link in project.links" v-bind:key="link.platform">
                                                    <i v-if="link.platform == 'GitHub'" class="bx bxl-github projectsItemLink"></i>
                                                    <i v-else-if="link.platform == 'DesktopApp'" class="bx bxl-windows projectsItemLink"></i>
                                                    <i v-else-if="link.platform == 'GoogleChrome'" class="bx bxl-chrome projectsItemLink"></i>
                                                    <i v-else-if="link.platform == 'AndroidApp'" class="bx bxl-android projectsItemLink"></i>
                                                </a>
                                            </p>
                                            <p class="projectsItemDescription">{{ project.description }}</p>
                                            <p class="projectsItemTechnologies d-flex flex-wrap">
                                                <i class="projectsItemTechnology" v-for="tech in project.technologies" v-bind:key="tech">{{ tech }}</i>
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

    export default {
        name : "Projects",
        data()
        {
            return {
                projects : 
                [
                    {
                        id : 1,
                        title : 'GzKitchen',
                        description : 'GzKitchen is an application for japanese style restaurant',
                        image : require('../assets/images/GzKitchen.png'),
                        demoLink : 'https://github.com/gavrielsatrio/GzKitchen/blob/main/GzKitchen-Setup.msi',
                        links :
                        [
                            {
                                platform : 'GitHub',
                                url : 'https://github.com/gavrielsatrio/GzKitchen'
                            },
                            {
                                platform : 'DesktopApp',
                                url : 'https://github.com/gavrielsatrio/GzKitchen/blob/main/GzKitchen-Setup.msi'
                            }
                        ],
                        technologies : ['C#', 'LINQ']
                    },
                    {
                        id : 2,
                        title : 'Flappy Dragon',
                        description : 'It\'s flappy bird but it\'s dragon',
                        image : require('../assets/images/Flappy Dragon.png'),
                        demoLink : 'http://gavrielsatrio.github.io/FlappyDragon',
                        links :
                        [
                            {
                                platform : 'GitHub',
                                url : 'https://github.com/gavrielsatrio/FlappyDragon'
                            },
                            {
                                platform : 'GoogleChrome',
                                url : 'http://gavrielsatrio.github.io/FlappyDragon'
                            }
                        ],
                        technologies : ['JavaScript', 'HTML', 'CSS']
                    },
                    {
                        id : 3,
                        title : 'GzKitchen Android',
                        description : 'Android version of GzKitchen',
                        image : require('../assets/images/GzKitchen.png'),
                        demoLink : 'https://github.com/gavrielsatrio/GzKitchen-Android/blob/main/GzKitchen.apk',
                        links :
                        [
                            {
                                platform : 'GitHub',
                                url : 'https://github.com/gavrielsatrio/GzKitchen-Android'
                            },
                            {
                                platform : 'AndroidApp',
                                url : 'https://github.com/gavrielsatrio/GzKitchen-Android/blob/main/GzKitchen.apk'
                            }
                        ],
                        technologies : ['Java', 'XML']
                    },
                    {
                        id : 4,
                        title : 'Froggy Knight',
                        description : 'Demonstrates how a knight move in chess from one place to another place',
                        image : require('../assets/images/Froggy Knight.png'),
                        demoLink : 'http://gavrielsatrio.github.io/FroggyKnight',
                        links :
                        [
                            {
                                platform : 'GitHub',
                                url : 'https://github.com/gavrielsatrio/FroggyKnight'
                            },
                            {
                                platform : 'GoogleChrome',
                                url : 'http://gavrielsatrio.github.io/FroggyKnight'
                            }
                        ],
                        technologies : ['Vue', 'JavaScript', 'Bootstrap', 'Algorithm']
                    },
                    {
                        id : 5,
                        title : 'LKS Mart',
                        description : 'LKSMart is a proposed self-service desktop application to help customer in order and buy process.',
                        image : require('../assets/images/LKS Mart.png'),
                        demoLink : 'https://github.com/gavrielsatrio/LKSMart',
                        links :
                        [
                            {
                                platform : 'GitHub',
                                url : 'https://github.com/gavrielsatrio/LKSMart'
                            },
                            {
                                platform : 'DesktopApp',
                                url : 'https://github.com/gavrielsatrio/LKSMart'
                            }
                        ],
                        technologies : ['C#', 'LINQ']
                    },
                    {
                        id : 6,
                        title : 'MacUI',
                        description : 'Beautiful Mac-like design windows forms application library',
                        image : require('../assets/images/MacUI.png'),
                        demoLink : 'https://github.com/gavrielsatrio/MacUI/blob/main/MacUI.dll',
                        links :
                        [
                            {
                                platform : 'GitHub',
                                url : 'https://github.com/gavrielsatrio/MacUI'
                            },
                            {
                                platform : 'DesktopApp',
                                url : 'https://github.com/gavrielsatrio/MacUI'
                            }
                        ],
                        technologies : ['C#', 'Canvas']
                    }
                ]
            }
        },
        mounted()
        {
            this.LoadHeader();
            this.LoadProjectsItem();
        },
        methods :
        {
            LoadHeader()
            {
                setTimeout(() =>
                {
                    var header = document.querySelector('#projectsHeader');
                    header.style.opacity = '1';
                    header.style.transform = 'none';
                }, 200);
            },
            LoadProjectsItem()
            {
                document.querySelectorAll('.projectsItem').forEach((item, index) =>
                {
                    setTimeout(() =>
                    {
                        item.style.opacity = '1';
                        item.style.transform = 'none';

                        setTimeout(() =>
                        {
                            item.style.transition = "all 0.3s ease-in-out";
                        }, 1000);
                    }, index * 200);
                });
            },
            OpenProject(url)
            {
                window.open(url).focus();
            }
        }
    }

</script>
<style>

    #projectsContainer
    {
        height: 100vh;
        overflow-y: auto;
        overflow-x: hidden;
    }

    #projectsContainer::-webkit-scrollbar
    {
        width: 0px;
    }

    #projectsContainer::-webkit-scrollbar-track
    {
        background-color: none;
        border-radius: 5px;
    }

    #projectsContainer::-webkit-scrollbar-thumb
    {
        background-color: none;
        border-radius: 5px;
    }

    #projectsHeader
    {
        font-family: futura;
        color: white;

        transition: all 1s ease-in-out;
        -webkit-transition: all 1s ease-in-out;
        -moz-transition: all 1s ease-in-out;
        -o-transition: all 1s ease-in-out;
        -ms-transition: all 1s ease-in-out;

        transform: translate(-50px, 0);
        opacity: 0;
    }

    #projectsItemList
    {
        padding: 25px 8px;
    }
    
    .projectsItem
    {
        background-color: white;
        border-radius: 10px;
        min-height: 400px;
        margin-bottom: 25px;
        transform: translate(0px, -100px);
        opacity: 0;
        cursor: pointer;
        box-sizing: border-box;
        box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.1);
        
        transition: all 1s ease-in-out;
        -webkit-transition: all 1s ease-in-out;
        -moz-transition: all 1s ease-in-out;
        -o-transition: all 1s ease-in-out;
        -ms-transition: all 1s ease-in-out;
    }

    .projectsItem:hover
    {
        transition: all 0.3s ease-in-out;
        -webkit-transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        -ms-transition: all 0.3s ease-in-out;

        transform: translate(0px, -6px) !important;
    }

    .projectsItemImageDiv
    {
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        height: 150px;
        overflow: hidden;
    }

    .projectsItemImage
    {
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        width: 100%;
        height: 150px;
        object-fit: cover;
        object-position: 50% 50%;
    }

    .projectsItemContent
    {
        padding: 10px 20px;
        font-family: openSansRegular;
    }

    .projectsItemTitle
    {
        font-weight: bold;
        font-size: 20px;
        padding-bottom: 10px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.15);
        margin: 0;
    }

    .projectsItemDescription
    {
        font-family: adelleLight;
        color: rgb(150, 150, 150);
        font-size: 14px;
        overflow-y: auto;
        font-weight: 100;
        height: 80px;
    }

    .projectsItemLinks
    {
        margin: 0;
        padding: 10px 0px;
    }

    .projectsItemLink
    {
        font-size: 22px;
        margin-right: 10px;
        color: rgb(204, 41, 62);
    }

    .projectsItemTechnologies
    {
        margin: 0;
        word-wrap: normal;
    }

    .projectsItemTechnology
    {
        font-size: 14px;
        font-style: normal;
        background-color: rgba(255, 208, 0, 0.384);
        border-radius: 5px;
        padding: 5px 10px;
        margin-right: 10px;
        margin-bottom: 10px;
    }

</style>