<template>
    <div id="appView">
        <div class="container-fluid">
            <div id="appViewContainer">
                <div class="row">
                    <div class="col-md-3 col-12">
                        <!-- + -->
                        <!-- Nav Bar -->
                        <!-- + -->
                        <div id="navBar">
                            <div class="container-fluid d-flex">
                                <nav id="navBarContainer" class="w-100 navbar navbar-expand-md">
                                    <i class="bx bxs-dashboard navbar-toggler" id="btnCollapseNavBar" @click="ToogleNavBar()"></i>
                                    <div class="row collapse navbar-collapse pt-md-0" id="navBarLinksRow">
                                        <div class="col-md-12 d-md-block d-none navBarSpace"></div>
                                        <div class="col-md-12 navBarLinksList">
                                            <div class="row h-100">
                                                <div class="d-flex col-md-12 col-12 align-items-center navBarLinksDiv">
                                                    <div class="d-flex navBarLinks navBarLinkSelected" id="home">
                                                        <div class="d-flex navBarLinkDot justify-content-center align-items-center">
                                                            <i class="bx bxs-home navBarLinkDotIcon"></i>
                                                        </div>
                                                        <p class="navBarLinkText">Home</p>
                                                    </div>
                                                </div>
                                                <div class="d-flex col-md-12 col-12 align-items-center navBarLinksDiv">
                                                    <div class="d-flex navBarLinks" id="projects">
                                                        <div class="d-flex navBarLinkDot justify-content-center align-items-center">
                                                            <i class="bx bxs-folder-open navBarLinkDotIcon"></i>
                                                        </div>
                                                        <p class="navBarLinkText">Projects</p>
                                                    </div>
                                                </div>
                                                <div class="d-flex col-md-12 col-12 align-items-center navBarLinksDiv">
                                                    <div class="d-flex navBarLinks" id="aboutMe">
                                                        <div class="d-flex navBarLinkDot justify-content-center align-items-center">
                                                            <i class="bx bxs-user navBarLinkDotIcon"></i>
                                                        </div>
                                                        <p class="navBarLinkText">About Me</p>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-md-12 d-md-block d-none navBarSpace"></div>
                                    </div>
                                </nav>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-9 col-12">
                        <!-- + -->
                        <!-- Content -->
                        <!-- + -->
                        <component :is="componentSelected"></component>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script src="https://getbootstrap.com/docs/4.0/dist/js/bootstrap.min.js"></script>
<script>

    import Home from './Home.vue';
    import Projects from './Projects.vue';
    import AboutMe from './AboutMe.vue';

    export default {
        name : "AppView",
        data()
        {
            return {
                componentSelected : 'home'
            }
        },
        components : {
            'home' : Home,
            'projects' : Projects,
            'aboutMe' : AboutMe
        },
        mounted()
        {
            var ref = this;

            document.querySelectorAll('.navBarLinks').forEach((item) =>
            {
                item.onclick = function()
                {
                    document.querySelectorAll('.navBarLinks').forEach((item) =>
                    {
                        item.classList.remove('navBarLinkSelected');
                    });

                    item.classList.add('navBarLinkSelected');
                    ref.componentSelected = item.id;
                }
            });

            setTimeout(() =>
            {
                document.querySelector("#btnCollapseNavBar").style.opacity = "1";
                document.querySelector("#navBarLinksRow").style.opacity = "1";
                document.querySelector("#navBarLinksRow").style.transform = "none";
            }, 500);
        },
        methods :
        {
            ToogleNavBar()
            {
                var navBarLinksRow = document.querySelector("#navBarLinksRow");
                if(navBarLinksRow.classList.contains("collapse"))
                {
                    navBarLinksRow.classList.remove("collapse");
                }
                else
                {
                    navBarLinksRow.classList.add("collapse");
                }
            }
        }
    }

</script>
<style>

    #appView
    {
        background-color: rgb(204, 41, 62);
    }

    #navBarContainer
    {
        position: relative;
        font-family: openSansRegular;
        min-height: 60px;
        padding: 0;
        padding-left: 0.5rem;
    }

    #navBarLinksRow
    {
        height: 100%;
        padding-top: 60px;
        transform: translate(0, -50px);
        opacity: 0;

        transition: all 1s ease-in-out;
        -webkit-transition: all 1s ease-in-out;
        -moz-transition: all 1s ease-in-out;
        -o-transition: all 1s ease-in-out;
        -ms-transition: all 1s ease-in-out;
    }

    .navBarSpace
    {
        height: 30vh;
    }

    .navBarLinksList
    {
        height: 40vh;        
    }

    .navBarLinksDiv
    {
        overflow: hidden;
    }

    .navBarLinks
    {
        cursor: pointer;
        color: white;
    }

    #btnCollapseNavBar
    {
        cursor: pointer;
        color: white;
        font-size: 20px;
        position: absolute;
        top: 25px;
        left: 0px;
        z-index: 1;
        opacity: 0;
        padding: 0;

        transition: all 1s ease-in-out;
        -webkit-transition: all 1s ease-in-out;
        -moz-transition: all 1s ease-in-out;
        -o-transition: all 1s ease-in-out;
        -ms-transition: all 1s ease-in-out;
    }

    .navBarLinkDot
    {
        background-color: white;
        width: 24px;
        height: 24px;
        border-radius: 50%;
        margin-right: 20px;
        transition: 0.5s all ease-in-out;
        overflow: hidden;
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
    }

    .navBarLinkDotIcon
    {
        position: relative;
        color: rgb(204, 41, 62);
        transition: 1s all ease-in-out;
        transform: translate(0, -30px);
    }

    .navBarLinkText
    {
        margin: 0;
        transition: 0.5s all ease-in-out;
        white-space: nowrap;
    }

    .navBarLinkSelected > .navBarLinkDot
    {
        width: 32px;
        height: 32px;
    }

    .navBarLinkSelected > .navBarLinkText
    {
        font-weight: bold;
        font-size: 20px;
    }

    .navBarLinkSelected > .navBarLinkDot > .navBarLinkDotIcon
    {
        transform: translate(0, 0);
    }

</style>