import Vue from 'vue'
import VueRouter from 'vue-router'
import AppView from '../views/AppView.vue';
import FlappyDragon from '../views/FlappyDragon.vue'

Vue.use(VueRouter)

const routes = [
    {
        path : '/',
        name : 'AppView',
        component : AppView
    },
    {
        path: '/FlappyDragon',
        name : 'FlappyDragon',
        component: FlappyDragon
    },
    {
        path: '*',
        redirect: '/'
    }
]

const router = new VueRouter(
    {
        mode : 'history',
        routes
    }
)

export default router
